<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/userInfo' }">个人信息</el-breadcrumb-item>
      <el-breadcrumb-item>修改密码</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <label>
        <span>旧密码：</span>
        <el-input 
          type="password" 
          v-model="oldPassword" 
          placeholder="请输入旧密码"></el-input>
      </label>
      <label>
        <span>新密码：</span>
        <el-input 
          type="password"
          v-model="newPassword" 
          placeholder="请输入新密码"></el-input>
      </label>
      <label>
        <span>请再次确认新密码：</span>
        <el-input 
          type="password" 
          v-model="checkPassword" 
          placeholder="请再次输入新密码"></el-input>
      </label>
      <el-button type="primary" @click="modify" :loading="submiting">提交</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      oldPassword: '',
      newPassword: '',
      checkPassword: '',
      submiting: false
    };
  },

  methods: {
    modify () {
      if (this.oldPassword === '') {
        this.$message('请输入旧密码')
      }
      else if (this.newPassword === '') {
        this.$message('请输入新密码')
      }
      this.submiting = true;
    }
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-top: 20px;
}

.container {
  margin-top: 40px;
  width: 600px;
  text-align: center;
}

label {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  color: #303133;
}

.el-input {
  width: 450px;
}

.el-button {
  width: 600px;
}
</style>





