<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/manageNotice'}">通知管理</el-breadcrumb-item>
      <el-breadcrumb-item>发布通知</el-breadcrumb-item>
    </el-breadcrumb>
    <p>发布新的通知</p>
    <el-button
      type="primary"
      @click="submitNotice"
      :loading="submiting"
      icon="el-icon-edit-outline">
      发布
    </el-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      submiting: false
    };
  },

  methods: {
    submitNotice  () {
      this.submiting = true;
    }
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-top: 20px;
}

p {
  margin-top: 20px;
  text-align: center;
}

.el-button {
  position: relative;
  left: 50%;
  margin-top: 100px;
  transform: translateX(-50%);
}
</style>





