<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>通知管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="top">
      <div class="search">
        <el-select v-model="department" placeholder="请选择搜索部门">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-input v-model="keyword" placeholder="请输入搜索关键词"></el-input>
        <el-button 
          type="primary"
          @click="searchNotice" 
          icon="el-icon-search">
          搜索
        </el-button>
        <el-button
          @click="reset" 
          icon="el-icon-refresh">
          重置
        </el-button>
      </div>
      <el-button 
        type="primary"
        @click="newNotice" 
        icon="el-icon-plus">
        发布新通知
      </el-button>
    </div>
    <p v-loading="loading">这是搜索的通知</p>
  </div>
</template>

<script>
export default {
  data () {
    return {
      department: '',
      options: [
        {
          label: '学工部',
          value: 1
        },
        {
          label: '体育部',
          value: 2
        }
      ],
      keyword: '',
      loading: true
    };
  },

  methods: {
    reset () {
      this.loading = false;
    },
    searchNotice () {
      if (this.department === '') {
        this.$message('请选择搜索部门');
      }
      else if (this.keyword === '') {
        this.$message('请输入关键词');
      }
    },
    newNotice () {
      this.$router.push('/newNotice');
    }
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-top: 20px;
}

.top {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-top: 20px;
}

.el-input {
  width: 250px;
  margin: 0 50px;
}

p {
  margin-top: 100px;
  text-align: center;
}
</style>



