<template>
  <el-container>
    <el-header>
      电子科技大学学工部工作系统
    </el-header>
    <el-main>
      <div class="login-area">
        <label>
          <span>用户名：</span>
          <el-input placeholder="请输入用户名" autofocus v-model="username"></el-input>
        </label>
        <label>
          <span>密码：</span>
          <el-input type="password" placeholder="请输入密码" v-model="password"></el-input>
        </label>
        <el-button type="primary" @click="login">登录</el-button>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    login () {
    if (this.username === '') {
      this.$message('请输入用户名');
      return;
    }
    if (this.password === '') {
      this.$message('请输入密码');
      return;
    }
    this.$router.push('/index');
  }
  }
  
}
</script>

<style scoped>
.el-header {
  line-height: 60px;
  text-align: center;
  color: #fff;
  background: #409EFF;
}

.el-main {
  color: #606266;
}

.login-area {
  display: flex;
  flex-flow: column nowrap;
  width: 425px;
  margin: 70px auto 0 auto;
  padding: 40px;
  border: 1px solid #d1dbe5;
  border-radius: 3px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
}

.login-area:hover {
  box-shadow: 0 3px 5px 0 rgba(0,0,0,.16), 0 0 7px 0 rgba(0,0,0,.06);
}

label {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.el-input {
  width: 350px;
}
</style>



