import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/pages/Login.vue';
import BaseLayout from '@/components/BaseLayout.vue';
import IndexPage from '@/pages/Index.vue';
import ManageNotice from '@/pages/ManageNotice.vue';
import NewNotice from '@/pages/NewNotice.vue';
import ManageDownloadFile from '@/pages/ManageDownloadFile.vue';
import ManageWorkFile from '@/pages/ManageWorkFile.vue';
import UserInfo from '@/pages/UserInfo.vue';
import ModifyPassword from '@/pages/ModifyPassword.vue';
import ManageDepartment from '@/pages/ManageDepartment.vue';
import ManageUser from '@/pages/ManageUser.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/login',
      component: Login
    },
    {
      path: '/',
      redirect: '/login',
      component: BaseLayout,
      children: [
        {
          path: '/index',
          component: IndexPage
        },
        {
          path: '/manageNotice',
          component: ManageNotice
        },
        {
          path: '/newNotice',
          component: NewNotice
        },
        {
          path: '/manageDownloadFile',
          component: ManageDownloadFile
        },
        {
          path: '/manageWorkFile',
          component: ManageWorkFile
        },
        {
          path: '/userInfo',
          component: UserInfo
        },
        {
          path: '/modifyPassword',
          component: ModifyPassword
        },
        {
          path: '/manageDepartment',
          component: ManageDepartment
        },
        {
          path: '/manageUser',
          component: ManageUser
        }
      ]
    }
  ]
});



