<template>
  <el-container>
    <el-header>
      <span class="title" @click="backToIndex">电子科技大学学工部工作系统</span>
      <div>
        <span>欢迎你</span>
        <span class="logout" @click="logout">注销</span>
      </div>
    </el-header>
    <el-container>
      <el-aside width="231px">
        <el-menu router>
          <el-menu-item index="/manageNotice">
            <i class="el-icon-info"></i>
            通知管理
          </el-menu-item>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-document"></i>
              <span>文件管理</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/manageDownloadFile">下载区文件管理</el-menu-item>
              <el-menu-item index="/manageWorkFile">工作区文件管理</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-menu-item index="/userInfo">
            <i class="el-icon-setting"></i>
            个人信息
          </el-menu-item>
          <el-submenu index="4">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>管理员</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/manageDepartment">部门管理</el-menu-item>
              <el-menu-item index="/manageUser">用户管理</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <el-main>
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </el-main>
        <el-footer height="40px">© Powered by 星辰工作室</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {

    }
  },

  methods: {
    backToIndex () {
      this.$router.push('/index')
    },

    logout () {
      this.$router.replace('/login')
    }
  }
}
</script>

<style scoped>
.el-header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  line-height: 60px;
  color: #fff;
  background: #409EFF;
}

.title {
  cursor: pointer;
}

.logout {
  position: relative;
  z-index: 1;
  margin-left: 20px;
  padding: 5px 15px;
  cursor: pointer;
  transition: color 0.3s;
}

.logout::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  transform: scaleY(0);
  transform-origin: 0 100%;
  transition: transform 0.3s;
}

.logout:hover {
  color: #409EFF;
}

.logout:hover::after {
  transform: scaleY(1);
  transform-origin: 0 0;
}

.el-menu {
  width: 230px;
  height: calc(100vh - 60px);
}

.el-footer {
  line-height: 40px;
  text-align: center;
  color: #909399;
}
</style>





