<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>个人信息</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="top">
      <el-button
        type="primary"
        @click="logout">
        注销
      </el-button>
      <el-button
        type="danger"
        @click="modifyPassword">
        修改密码
      </el-button>
    </div>
    <p>这是个人信息管理</p>
  </div>
</template>

<script>
export default {
  methods: {
    logout () {
      this.$router.replace('/login');
    },
    modifyPassword () {
      this.$router.push('/modifyPassword');
    }
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-top: 20px;
}

.top {
  margin-top: 40px;
}

p {
  margin-top: 20px;
  text-align: center;
}
</style>



